<template>
  <v-main>
    <v-parallax
        dark
        height="200"
        src="@/assets/paralax.jpg"
    >
      <v-overlay
          value="true"
          absolute
      >
        <v-row
            align="center"
            justify="center"
        >
          <v-col
              cols="12"
          >
            <v-img style="margin: 0 auto" max-width="150px" src="@/assets/logo.svg"></v-img>
          </v-col>
        </v-row>
      </v-overlay>
    </v-parallax>
    <v-row
        align="center"
        justify="center"
    >

      <v-col
          class="text-center"
          cols="12"
          style="margin-top: 20px;"
      >
        <v-btn
            x-large
            color="warning"
            dark
            @click="goBack"
        >
          ZURÜCK
        </v-btn>
      </v-col>
    </v-row>
    <v-container>
      <v-row style="margin-bottom: 25px;">
        <v-col
            class="text-center"
            cols="12"
        >
          <h1 style="text-decoration: underline">Speisen</h1>
        </v-col>
      </v-row>
      <v-row v-for="food in this.$store.state.foods" v-bind:key="food[0]" style="margin-bottom: 25px;">
        <v-col
            class="text-center"
            cols="12"
        >
          <h2 style="text-decoration: underline">{{ food[0] }}</h2>
        </v-col>
        <v-col
            cols="12"
        >
          <v-row v-for="item in food[1]" v-bind:key="item.name">
            <v-col cols="9">
              <span v-if="item.soldOut === 'TRUE'" style="text-decoration-line: line-through; text-decoration-color: red;">{{ item.name }}</span>
              <span v-else>{{ item.name }}</span>
              <span v-if="item.soldOut === 'TRUE'" class="desc" style="color: red;"> AUSVERKAUFT</span>
            </v-col>
            <v-col cols="3" style="text-align: right"><span v-if="item.price !== ''">{{ item.price }} €</span></v-col>
            <v-col cols="12" style="margin-top:-30px">
              <span v-if="item.description !== ''" class="desc">({{ item.description }})</span>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </v-main>
</template>

<script>
import router from "@/router";

export default {
  name: "Menu",
  created() {
    this.$store.dispatch('getFoodDataFromSheets');
  },
  methods: {
    goBack() {
      router.push('/')
    }
  }
}
</script>

<style scoped>
  .row + .row {
    margin-top: 0;
  }

  .desc {
    font-size: 12px;
    color: dimgrey;
  }
</style>