import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Drinks from '../views/Getränke.vue';
import Food from '../views/Speisen.vue';
import Hookahs from '../views/Shishas.vue';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/drinks',
    name: 'Getränke',
    component: Drinks
  },
  {
    path: '/food',
    name: 'Speisen',
    component: Food
  },
  {
    path: '/hookahs',
    name: 'Shishas',
    component: Hookahs
  },
]

const router = new VueRouter({
  routes
})

export default router
