import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      events: [],
      drinks: [],
      foods: [],
      hookahs: []
  },
  mutations: {
      saveDrinksIntoState (state, payload) {
          let newArr = payload.data.values.map(item => {
              if (item[5] !== undefined) {
                  return Object.assign({}, {
                      name: item[0],
                      description: item[1],
                      liter: item[2],
                      price: item[3],
                      categorie: item[4],
                      soldOut: item[5]
                  })
              }
          })

          let arr = newArr.filter(function(element) {
              return element !== undefined;
          })

          arr.shift()

          function groupBy(list, keyGetter) {
              const map = new Map();
              list.forEach((item) => {
                  const key = keyGetter(item);
                  const collection = map.get(key);
                  if (!collection) {
                      map.set(key, [item]);
                  } else {
                      collection.push(item);
                  }
              });
              return map;
          }

          const grouped = groupBy(arr, item => item.categorie);

          state.drinks = Array.from(grouped)
      },
      saveFoodIntoState (state, payload) {
          let newArr = payload.data.values.map(item => {
              if (item[4] !== undefined) {
                  return Object.assign({}, {
                      name: item[0],
                      description: item[1],
                      price: item[2],
                      categorie: item[3],
                      soldOut: item[4]
                  })
              }
          })

          let arr = newArr.filter(function(element) {
              return element !== undefined;
          })

          arr.shift()

          function groupBy(list, keyGetter) {
              const map = new Map();
              list.forEach((item) => {
                  const key = keyGetter(item);
                  const collection = map.get(key);
                  if (!collection) {
                      map.set(key, [item]);
                  } else {
                      collection.push(item);
                  }
              });
              return map;
          }

          const grouped = groupBy(arr, item => item.categorie);

          state.foods = Array.from(grouped)
      },
      saveEventIntoState (state, payload) {
          let newArr = payload.data.values.map(item => {
              if (item[1] !== undefined) {
                  return Object.assign({}, {
                      text: item[0],
                      activated: item[1],
                  })
              }
          })

          let arr = newArr.filter(function(element) {
              return element !== undefined;
          })

          arr.shift()

          state.events = arr
      },
      saveShishasIntoState (state, payload) {
          let newArr = payload.data.values.map(item => {
              if (item[3] !== undefined) {
                  return Object.assign({}, {
                      tobacco: item[0],
                      taste: item[1],
                      categorie: item[2],
                      soldOut: item[3]
                  })
              }
          })

          let arr = newArr.filter(function(element) {
              return element !== undefined;
          })

          arr.shift()

          function groupBy(list, keyGetter) {
              const map = new Map();
              list.forEach((item) => {
                  const key = keyGetter(item);
                  const collection = map.get(key);
                  if (!collection) {
                      map.set(key, [item]);
                  } else {
                      collection.push(item);
                  }
              });
              return map;
          }

          const grouped = groupBy(arr, item => item.categorie);

          state.hookahs = Array.from(grouped)
      },
  },
  actions: {
    getDrinksDataFromSheets: function ({commit}) {
      axios.get('https://sheets.googleapis.com/v4/spreadsheets/1r4zdv5tQ_WC_d-HIssTpXJWFdqlAgu7AEZVuj-hGIVc/values/Getränke?alt=json&key=AIzaSyB1aXZYCKMJKHLR19xOEwEoAx2kr_K33pc')
          .then(function (response) {
            commit('saveDrinksIntoState', response)
          })
          .catch(function (error) {
            console.log(error)
          })
    },
      getFoodDataFromSheets: function ({commit}) {
          axios.get('https://sheets.googleapis.com/v4/spreadsheets/1r4zdv5tQ_WC_d-HIssTpXJWFdqlAgu7AEZVuj-hGIVc/values/Speisen?alt=json&key=AIzaSyB1aXZYCKMJKHLR19xOEwEoAx2kr_K33pc')
              .then(function (response) {
                  commit('saveFoodIntoState', response)
              })
              .catch(function (error) {
                  console.log(error)
              })
      },
      getEventsDataFromSheets: function ({commit}) {
          axios.get('https://sheets.googleapis.com/v4/spreadsheets/1r4zdv5tQ_WC_d-HIssTpXJWFdqlAgu7AEZVuj-hGIVc/values/Events?alt=json&key=AIzaSyB1aXZYCKMJKHLR19xOEwEoAx2kr_K33pc')
              .then(function (response) {
                  commit('saveEventIntoState', response)
              })
              .catch(function (error) {
                  console.log(error)
              })
      },
      getShishasDataFromSheets: function ({commit}) {
          axios.get('https://sheets.googleapis.com/v4/spreadsheets/1r4zdv5tQ_WC_d-HIssTpXJWFdqlAgu7AEZVuj-hGIVc/values/Shishas?alt=json&key=AIzaSyB1aXZYCKMJKHLR19xOEwEoAx2kr_K33pc')
              .then(function (response) {
                  commit('saveShishasIntoState', response)
              })
              .catch(function (error) {
                  console.log(error)
              })
      },
  },
  modules: {
  }
})
